<template>

    <main class="content container">
      <div class="content__top content__top--catalog">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          152 товара
          {{color}}
        </span>
      </div>
  
      <div class="content__catalog">
        <productFilterVue :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color.sync="filterColor"></productFilterVue>
  
        <section class="catalog">

          <div v-if="productsLoading" class="d-flex justify-content-center">
            <div class="spinner-border" role="status">
              <span class="visually-hidden">Загрузка...</span>
            </div>
          </div>
          <div v-if="productsLoadingFailed">
            Произошла ошибка при загрузке товаров
            <button @click.prevent='loadProducts'>Попробовать еще раз</button>
          </div>
  
          <ProductList :products="products"/>
          <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
  
        </section>
  
      </div>
    </main>
  
  </template>

<script>

import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import productFilterVue from '@/components/productFilter.vue';
import eventBus from '@/eventBus';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  created() {
    eventBus.$on('submit', (data) => {
      this.color = data;
    });
    this.loadProducts();
    this.loadColors();
  },
  components: { ProductList, BasePagination, productFilterVue },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: '',
      color: '',
      colorData: null,
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    
    products() {
      return this.productsData 
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
          color: product.colors.map((item) => item.code),
        }
        )) : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadColors() {
      clearTimeout(this.loadColor);
      this.loadColor = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/colors`)
          .then((response) => { this.colorData = response.data.items; });
      }, 0);
    },
    filterColorId() {
      return this.filterColor.id; 
    },
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId(),
            },
          })
          .then((response) => { this.productsData = response.data; })
          // eslint-disable-next-line no-return-assign
          .catch(() => this.productsLoadingFailed = true)
          // eslint-disable-next-line no-return-assign
          .then(() => this.productsLoading = false);
      }, 2000);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
    
  },
};
</script>
