<template>
    <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ 23621</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{this.$store.state.orderInfo.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{this.$store.state.orderInfo.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{this.$store.state.orderInfo.phone}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{this.$store.state.orderInfo.email}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="(product, index) in this.$store.state.orderInfo.basket.items" :key="index">
              <h3>{{product.product.title}}</h3>
              <b>{{product.price}} ₽</b>
              <span>Артикул: {{product.id}}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{this.totalProduct()}}</b> товара на сумму <b>{{this.totalPrice()}} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
export default {
  methods: {
    // eslint-disable-next-line consistent-return
    totalProduct() {
      if (this.$store.state.orderInfo !== 0) {
        const arr = [];
        this.$store.state.orderInfo.basket.items.forEach((item) => {
          arr.push(item.quantity);
        });
        return arr.reduce((acc, iteM) => (acc + iteM));
      }
    },
    // eslint-disable-next-line consistent-return
    totalPrice() {
      if (this.$store.state.orderInfo) {
        return this.$store.state.orderInfo.basket.items.reduce((acc, item) => (item.product.price * item.quantity) + acc, 0);
      }
    },
  },
  created() {
    console.log(this.$store.state.orderInfo);
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
        
};
</script>
