<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
    <li class="cart__item product">
              <div class="product__pic">
                <img :src="item.product.image" width="120" height="120" alt="item.product.title">
              </div>
              <h3 class="product__title">
                {{item.product.title}}
              </h3>
              <span class="product__code">
                Артикул: {{item.product.id}}
              </span>

              <div class="product__counter form__counter">
                <button type="button" aria-label="Убрать один товар" @click="countProductsMinus">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="amount" name="count" @input="validationProduct" @blur="validationProduct">

                <button type="button" aria-label="Добавить один товар" @click="countProductsPlus">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <b class="product__price">
                {{item.amount * item.product.price | numberFormat}} ₽
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapMutations, mapActions } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
    ...mapActions({ deleteProduct: 'deleteCartProductAmount' }),
    countProductsPlus() {
      this.amount += 1;
    },
    countProductsMinus() {
      // eslint-disable-next-line no-unused-expressions
      this.amount === 0 ? this.amount === 0 : this.amount -= 1;
    },
    validationProduct() {
      // eslint-disable-next-line no-unused-expressions
      this.amount < 0 ? this.amount = 0 : this.amount;
    },
  },
};
</script>
