<!-- eslint-disable vuejs-accessibility/click-events-have-key-events -->
<template>
   <li class="catalog__item">
        <router-link class="catalog__pic" :to="{name:'product', params: {id:product.id}}">
          <img :src="product.image" :alt="product.title">
        </router-link>

        <h3 class="catalog__title">
          <a href="#">
            {{ product.title }}
          </a>
        </h3>

        <span class="catalog__price">
          {{ product.price | numberFormat }}
        </span>
        
        <ul class="colors colors--black" @click="submits">
              <li class="colors__item" v-for="(colors, index) in product.color" :key="index">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio"  :value="colors" v-model="color">
                  <span class="colors__value" v-bind:style="{'background-color': colors}">
                  </span>
                </label>
              </li>
            </ul>
      </li>
</template>

<script>
import goToPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import products from '@/data/products';
import eventBus from '@/eventBus';


export default {
  props: ['product', 'radio'],
  filters: {
    numberFormat,
  },
  data() {
    return {
      color: '#73B6EA',
      products,
      key: '',
    };
  },
  methods: {
    goToPage,
    submits() {
      eventBus.$emit('submit', this.color);
    },
    reset() {
      this.$emit('update:radio', '');
    },
  },
};
</script>
