<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
    <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>
    <div v-if="preloader" class="spinner-border" role="status">
        <span class="visually-hidden">Данные отправляются на сервер...</span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText  v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя"></BaseFormText>
            <BaseFormText title="Адрес доставки" :error="formError.address" placeholder="Введите ваш адрес" v-model="formData.address">
</BaseFormText>
            <BaseFormText title="Введите ваш телефон" :error="formError.phone" v-model="formData.phone" placeholder="Введите ваш телефон">
</BaseFormText>
            <BaseFormText title="Введи ваш Email" :error="formError.email"  v-model="formData.email" placeholder="Введи ваш Email">
</BaseFormText>
            <BaseFormTextarea title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment" placeholder="Ваши пожелания">
</BaseFormTextarea>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="(product, index) in orderList()" :key="index">
              <h3>{{product.product.title}}</h3>
              <b>{{product.price}} ₽</b>
              <span>Артикул: {{product.id}}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p v-if="this.totalProduct()">Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{this.totalProduct()}}</b> товара на сумму <b>{{this.totalPrice()}} ₽</b></p>
          </div>
          <button class="cart__button button button--primery" type="submit" v-if="this.totalProduct()">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
           {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import { API_BASE_URL } from '@/config';
import axios from 'axios';


export default {
  components: { BaseFormText, BaseFormTextarea },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      preloader: false,
    };
  },
  methods: {
    orderList() {
      console.log(this.$store.state.cartProducts);
      return this.$store.state.cartProductsData;
    },
    // eslint-disable-next-line consistent-return
    totalProduct() {
      if (this.$store.state.cartProducts.length !== 0) {
        const arr = [];
        this.$store.state.cartProducts.forEach((item) => {
          arr.push(item.amount);
        });
        return arr.reduce((acc, iteM) => (acc + iteM));
      }
    },
    // eslint-disable-next-line consistent-return
    totalPrice() {
      if (this.$store.getters.cartDetailProducts) {
        return this.$store.getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
      }
    },
    order() {
      this.preloader = true;
      return (new Promise((resolve) => { setTimeout(resolve, 2000); }))
        .then(() => {
          this.formError = {};
          this.formErrorMessage = '';
          axios
            .post(`${API_BASE_URL}/api/orders`, {
              ...this.formData,
            }, {
              params: {
                userAccessKey: this.$store.state.userAccessKey,
              },
            })
            .then((response) => {
              this.$store.commit('resetCart');
              this.$store.commit('updateOrderInfo', response.data);
              this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
              this.preloader = false;
            })
            .catch((error) => {
              this.preloader = false;
              this.formError = error.response.data.error.request || {};
              this.formErrorMessage = error.response.data.error.message;
            });
        });
    },
  },
};
</script>
